<template>
    <div class="item">
        <img src="http://m.taomeixh.com/upload/goods/19/19_69be4e25dfcf390e1d1338125aca69c1_350.jpg" alt="">
        <h5 class="title">纯真之恋 —9支戴安娜玫瑰加浅紫勿忘我花束</h5>
        <div class="card_footer">
            <div class="price">
                <span class="aui-text-pink">￥168.00</span>
                <span class="buy_num" v-if="buynum >= 0">{{buynum}}人购买</span>
            </div>
            
            <div class="list_icon">
                <img src="/static/icon/心.png" alt="">
                <img src="/static/icon/star.png" alt="">
                <img src="/static/icon/cart.png" alt="">
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props:{
            buynum:Number
        }
    }
</script>

<style lang="stylus" scoped>
.item
    width 1.70rem
    height 2.33rem
    background-color #fff
    padding-bottom: 0.2rem;
    box-sizing border-box
    border-radius: 0.04rem;
    box-shadow: #ccc 0 0 3px;
    overflow: hidden;
    margin-top .08rem
    img 
        width 1.68rem
        height 1.68rem
    .title
        font-size .14rem
        height .35rem
        line-height .25rem
        padding 0 .04rem
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    .card_footer
        display flex
        justify-content space-between
        .price
            font-size .12rem
            display flex
            .aui-text-pink
                color #e91e63
                line-height .29rem
            .buy_num
                font-size .12rem
                color #757575
                line-height .29rem
                margin-left .05rem
        .list_icon
            width .54rem
            height .29rem
            font-size 0
            padding-top .08rem
            box-sizing border-box
            float right 
            img 
                width .14rem
                height .14rem
                margin-left .03rem
</style>


