<template>
    <div class="cate">
        <div class="cate_container">
            <div class="menu-wrapper" ref="menuWrapper">
                <ul>
                    <li v-for="(item, index) in 80" :key="index" :class="['menu-item',activeIndex === index?'active':'']" @click="changeActiveIndex(index)">
                        {{index + '鲜花'}}
                    </li>
                </ul>
            </div>
            <div class="cate-wrapper" v-show="activeIndex === index" v-for="(item, index) in 80" :key="index">
                <ul>
                    <li v-for="(item2, index2) in 8" :key="index2"  class="row">
                        <div class="title">绿植栽培{{index}}</div>
                        <div class="items">
                            <div class="item">
                                <img src="http://m.taomeixh.com/upload/cwap/category/category-pic-2693.jpg" alt="">
                                <div class="text">多肉植物{{index}}</div>
                            </div>
                            <div class="item">
                                <img src="http://m.taomeixh.com/upload/cwap/category/category-pic-2693.jpg" alt="">
                                <div class="text">多肉植物{{index + 1}}</div>
                            </div>
                            <div class="item">
                                <img src="http://m.taomeixh.com/upload/cwap/category/category-pic-2693.jpg" alt="">
                                <div class="text">多肉植物{{index + 2}}</div>
                            </div>
                            <div class="item">
                                <img src="http://m.taomeixh.com/upload/cwap/category/category-pic-2693.jpg" alt="">
                                <div class="text">多肉植物{{index + 3}}</div>
                            </div>
                        </div>
                    </li>
                </ul>
            </div>            
        </div>
    </div>
</template>

<script>
import BScroll from 'better-scroll'
    export default {
        data() {
            return {
                goods:[],
                activeIndex:0
            }
        },
        created(){
            this.goods = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
        },
        mounted(){
            let menuscroll = new BScroll('.menu-wrapper',{
                scrollY:true,
                click:true
            })
            // let catescroll = new BScroll('.cate-wrapper',{
            //     scrollY:true
            // })
            console.log(menuscroll);
            
        },
        updated(){
            // alert(1)
            console.log('changed');
            
        },
        methods: {
            changeActiveIndex(index) {
                this.activeIndex = index;
                console.log('xxx');
                
            }
        }
    }
</script>

<style lang="stylus" scoped>
.cate
    height calc(100% - .5rem)
    .cate_container
        height 100%
        width 100% 
        .menu-wrapper
            width .93rem
            height 100% 
            background-color #dddddd
            overflow hidden
            float left
            li 
                height .44rem
                // color rgb(234,41,106)
                padding-left .15rem
                font-size .16rem
                line-height .44rem
                box-sizing border-box
                // display flex
                // flex-direction column
                // align-items center
                // justify-content center
                overflow hidden
                position relative 
                color rgb(60,60,60)
                border-bottom 1px solid #6c757d6e
            .active
                color rgb(234,41,106)
                background-color #fff
                &::before
                    content ''
                    width .05rem
                    height .34rem
                    background-color rgb(234,41,106)
                    position absolute 
                    left 0
                    top .05rem
                    display inline-block
        .cate-wrapper
            width calc(100% - .93rem)
            height 100%
            // background-color yellow
            float right
            // overflow hidden
            overflow scroll
            padding .15rem
            box-sizing border-box
            .row 
                width 100%                
                .title
                    height .16rem
                    color #6c757d !important
                    font-weight bolder
                .items
                    width 100% 
                    display flex
                    flex-wrap wrap
                    text-align center
                    .item
                        width 33.333%
                        height 1.02rem
                        padding .05rem
                        box-sizing border-box
                        font-size .12rem
                        color rgb(60,60,60)
                        img 
                            width 100%
</style>


