<template>
    <div class="nav">
        <!-- <slot name="slo" :list3="list"></slot>
         -->
         <div class="nav_item" v-for="(item,index) in list" :key="index">
             <div v-if="item.img_url" class="nav_img">
                <img :src="item.img_url" alt="">
             </div>   
            <div v-else class="nav_img">
                <span>{{item.num}}</span>
             </div>        
            <div class="nav_text">{{item.text}}</div>
         </div>
    </div>
</template>

<script>
    export default {
        props: {
            list: {
                type: Array,
                default: []
            },
        },
    }
</script>

<style lang="stylus" scoped>
.nav
    display flex
    align-items center
    justify-content space-around
    // margin-top .1rem
    .nav_item
        display flex
        flex-direction column
        justify-content center
        align-content center
        text-align center
        width 100%
        .nav_img
            img 
                width .24rem
                height .24rem
        .nav_text
            margin-top .06rem
            color #3c3c3c
            font-size .14rem
            
</style>


