<template>
    <div class="login">
        <div class="content">
            <div class="login_logo">
                <div class="logo">
                    <img src="http://m.taomeixh.com/wapStyle/images/login_logo.png" alt="">
                </div>
            </div>
            <div class="mar_lr">
                <div class="login_qie_btn">
                    <div class="right active">
                        新用户注册
                        <div class="hr"></div>
                    </div>
                </div>
                <div class="login_form">
                    <div class="input">
                        <div class="login_label">
                            手机号
                        </div>
                        <input type="text">
                    </div>
                    <div class="input" v-show="isActive">
                        <input type="text" placeholder="验证码">
                        <div class="getyzm">
                            获取
                        </div>
                    </div>
                    <button class="btn">立即注册</button>
                    <!-- <span class="forget">
                        忘记密码？
                    </span> -->
                    <router-link to="/login" class="register"  tag="div">
                        去登录
                    </router-link>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                isActive: true
            }
        },
        methods:{
            changeTab(){
                this.isActive = !this.isActive
            }
        }
    }
</script>

<style lang="stylus" scoped>
.login
    .content
        background-image url(http://m.taomeixh.com/wapStyle/images/login_bg.png)
        width 100%
        padding .15rem
        box-sizing border-box
        .login_logo
            height 1.4rem
            padding-top .5rem
            width 100%
            box-sizing border-box
            .logo
                margin 0 auto
                width 1.72rem
                img 
                    width 100%
        .mar_lr
            padding 0 .4rem
           .login_qie_btn
                height .3rem
                // background-color red 
                font-size .18rem
                display flex
                color #A3A3A3
                .active
                    color rgb(236,72,125)
                .left,.right
                    line-height .28rem
                    flex 1
                    text-align center
                    .hr
                        width 20%
                        height .03rem
                        background-color rgb(236,72,125)
                        margin 0 auto 
            .login_form
                margin-top .4rem
                .input
                    margin-top .2rem
                    position relative
                    .login_label
                        font-size .16rem
                        height .24rem
                    input
                        border 0
                        border-bottom 1px solid #000
                        padding .08rem
                        width 100%
                        box-sizing border-box
                        opacity 0.5
                        &:focus
                            border-bottom 1px solid rgb(236,72,125)
                    .getyzm
                        position: absolute;
                        top: 0.02rem;
                        right: 0.04rem;
                        font-size: 0.14rem;
                        color: #9A9A9A;
                        border: solid 1px #9A9A9A;
                        padding: 0.065rem 0.08rem;
                        border-radius: 0.06rem;
                .btn
                    width 100%
                    height .36rem
                    background rgb(255,241,241)
                    color rgb(236,72,125)
                    border none
                    margin-top .4rem
                .forget
                    color rgb(236,72,125)
                    padding .05rem 0
                    font-size .14rem
                    display inline-block
                .register
                    color rgb(236,72,125)
                    text-align right 
                    margin-top .2rem

</style>


