<template>
    <div class="cart">
        <div v-if="false" class="cart_list">
            <div class="cart_item">
                <div class="cart_head">
                    <div class="check">                       
                        <van-checkbox v-model="checked" checked-color="rgb(212, 35, 123)"></van-checkbox>
                    </div>
                    <div class="dianpu">
                        淘美鲜花
                    </div>
                    <div class="bianji" @click="edit">
                        {{bianji}}
                    </div>
                </div>
                <div class="shop_list">
                    <div class="list_item">
                        <div :class="['item_box',is_show?'del_div':'']">
                            <div class="check">
                                <van-checkbox v-model="checked" checked-color="rgb(212, 35, 123)"></van-checkbox>
                            </div>
                            <div class="good_pic">
                                <img src="http://m.taomeixh.com/upload/goods/19/19_69be4e25dfcf390e1d1338125aca69c1_350.jpg" alt="">
                            </div>
                            <div class="good_info">
                                <div class="name">纯真之恋 —9支戴安娜玫瑰加浅紫勿忘我花束纯真之恋 —9支戴安娜玫瑰加浅紫勿忘我花束</div>
                                <div class="shop_list_footer">
                                    <div class="price">￥168.00</div>
                                    <van-stepper v-model="value" />
                                </div>
                            </div>
                        </div>
                        <div class="del">
                            删除
                        </div>
                    </div>
                </div>                
            </div>            
        </div>
        <div class="cart_empty">
            <div class="cart_kong_img">
                <img src="http://m.taomeixh.com/wapStyle/images/kong_cart.jpg" alt="">
            </div>
            <div class="cart_kong_text">
                <h4 class="text1">购物车是空的</h4>
                <h4 class="text2">快去<span class="">逛逛吧</span>
                </h4>
            </div>
        </div>
        <van-submit-bar
        :price="3050"
        button-text="提交订单"
        @submit="onSubmit"
        >
            <div class="left"></div>
            <van-checkbox v-model="checked2"  checked-color="rgb(212, 35, 123)">全选</van-checkbox>
            <span slot="tip">
                你的收货地址不支持同城送, <span>修改地址</span>
            </span>
        </van-submit-bar>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                list: ['a'],
                result: ['a', 'b'],
                checked: false,
                checked2: false,
                value: 1,
                imageURL:'https://img.yzcdn.cn/upload_files/2017/07/02/af5b9f44deaeb68000d7e4a711160c53.jpg',
                is_show: false,
                bianji:'编辑'
            }
        },
        methods: {
            onSubmit() {
                
            },
            edit() {
                this.is_show = !this.is_show;
                if(this.is_show){
                    this.bianji = '完成';
                }else{
                    this.bianji = '编辑';
                }
            }
        },
    }
</script>

<style lang="stylus" scoped>
.cart
    width 100% 
    .left
        width .2rem
        // height 100%
        min-height .2rem
    .del_div
        transition-duration 300ms
        transform translate3d(-.60rem, 0px, 0px)    
    .cart_empty
        width 100%
        .cart_kong_img
            width 100%
            img 
                width 100%  
        .cart_kong_text
            text-align center
            .text1
                font-size .18rem
                line-height .27rem
            .text2
                font-size .14rem
    .cart_list
        .cart_item
            margin-top .1rem
            .cart_head 
                width 100%
                height .315rem
                background-color #F3F3F3
                padding 0 .15rem
                display flex
                align-items center
                box-sizing border-box
                .check
                    flex 1
                .dianpu
                    flex 8
                .bianji
                    flex 1
                    text-align right 
            .shop_list
                margin-top .1rem
                width 100%
                .list_item
                    width 100%
                    height .7625rem
                    margin-top .15rem
                    position relative
                    .item_box
                        width 100%
                        height .7625rem
                        position absolute
                        left 0
                        top 0
                        display flex
                        align-items center
                        z-index 99
                        background-color #fff
                        .check
                            padding 0 .15rem
                            width .5rem
                        .good_pic
                            width .76rem
                            img 
                                width .76rem
                                height .76rem 
                        .good_info
                            width 2.49rem
                            height 100%
                            display flex
                            flex-direction column
                            justify-content space-between
                            padding-left .15rem
                            box-sizing border-box
                            padding-right .1rem                       
                            .name
                                width 100%                        
                                font-size .16rem
                                color rgb(60,60,60)
                                line-height .2rem
                                height .2rem
                                overflow: hidden;
                                text-overflow:ellipsis;
                                white-space: nowrap;
                            .shop_list_footer
                                display flex
                                align-items center
                                justify-content space-between
                                .price
                                    color rgb(236,72,125) 
                    .del
                        width .5rem
                        height 99%
                        background-color #e51c23
                        color #ffffff
                        position absolute
                        top .01rem
                        right .1rem
                        display flex
                        align-items center
                        justify-content center  
                        z-index 98   

</style>




