<template>
    <div class="index">
        <!-- <search-input/> -->
        <nav-bar/>
        <swiper/>
        <index-nav/>
        <list/>
        <list/>
        <list/>
    </div>
</template>

<script>
import SearchInput from '@/components/SearchInput'
import Swiper from '@/components/Swiper'
import NavBar from '@/components/NavBar'
import IndexNav from '@/components/IndexNav'
import List from './components/List'
const axios = require('axios');
export default {
    components:{
        Swiper,NavBar,IndexNav,List,SearchInput
    },
    created(){
        console.log('xxx');
        axios.get('http://api.tp5.test/index.php/token')
            .then(function (response) {
                // handle success
                console.log(response.data);
              
            })
            .catch(function (error) {
                // handle error
                console.log(error);
            })
            .then(function () {
                // always executed
            });
    }
}
</script>

<style lang="stylus" scoped>
$bgColor = rgb(234,41,106)
.index
    .test
        width 100%
        height 3.5rem
        background-color $bgColor
</style>



