<template>
    <div class="swiper">
        <swiper :options="swiperOption" ref="mySwiper" @someSwiperEvent="callback">
            <!-- slides -->
            <swiper-slide>
                <img src="https://img02.hua.com/slider/18_syz_m.jpg" alt="生日专题">
            </swiper-slide>
            <swiper-slide>
                <img src="https://img02.hua.com/slider/18_youflower_m.jpg" alt="生日专题">
            </swiper-slide>
            <swiper-slide>
                <img src="https://img02.hua.com/slider/18_birthday_m.jpg" alt="生日专题">
            </swiper-slide>
            <!-- Optional controls -->
            <div class="swiper-pagination"  slot="pagination"></div>
            <!-- <div class="swiper-button-prev" slot="button-prev"></div>
            <div class="swiper-button-next" slot="button-next"></div> -->
            <!-- <div class="swiper-scrollbar"   slot="scrollbar"></div> -->
        </swiper>
    </div>
</template>
<script>

export default {
    data() {
        return {
            swiperOption: {
            // some swiper options/callbacks
            // 所有的参数同 swiper 官方 api 参数
            // ...
                pagination: {
                    el: '.swiper-pagination'
                },
                loop : true,
                autoplay: {
                    delay: 3000,
                    stopOnLastSlide: false,
                    disableOnInteraction: true,
                },
            }
        }
    },
    methods: {
        callback() {
            
        }
    },
}
</script>

<style lang="stylus" scoped>
.swiper
    width 100%
    .swiper-wrapper
        img 
            width 3.75rem
            height 1.80rem
    .swiper-pagination-bullet-active
        background-color rgb(234,41,106)
</style>





