<template>
    <div class="goods">
        <v-header/>
        <div class="swiper">
            <swiper :options="swiperOption" ref="mySwiper" @someSwiperEvent="callback">
                <!-- slides -->
                <swiper-slide>
                    <img src="http://m.taomeixh.com/upload/goods/617/617_7453067f739b612a5708d82ddfa028c3.png" alt="生日专题">
                </swiper-slide>
            </swiper>
        </div>
        <div class="good_info">
            <div class="price">商城价：￥228.00</div>
            <del class="price2">市场价：298.00</del>
            <div class="col">
                <div class="title">33支玫瑰花束</div>
                <div class="text_pice">
                    33支玫瑰花束
                </div>
            </div>
        </div>
        <div class="hr"></div>
        <div class="fujia">
            <div class="row">
                <div class="xs2">花材：</div>
                <div class="xs10">玫瑰鲜花</div>
            </div>
            <div class="row">
                <div class="xs2">包装：</div>
                <div class="xs10">花束</div>
            </div>
            <div class="row">
                <div class="xs2">花语：</div>
                <div class="xs10">我爱你</div>
            </div>
            <div class="row">
                <div class="xs2">附加：</div>
                <div class="xs10"></div>
            </div>
        </div>
        <div class="shop">
            <div class="top">
                <div class="img">
                    <img src="http://m.taomeixh.com/upload/store/596/7e1a352f4ff96f71e43f5fb071b87dbe.jpg" alt="">
                </div>
                <div class="info">
                    <h5 class="title">诚心缘鲜花店
                        <span class="shop_logo">保</span>
                    </h5>
                    <div class="progress">
                        
                    </div>
                    <div class="shop_list">
                        <div class="item">
                            关注人数
                            <span class="text_pice">1人</span>
                        </div>
                        <div class="item">
                            全部商品
                            <span class="text_pice">4</span>
                        </div>
                        <div class="item">
                            店铺动态
                            <span class="text_pice">22</span>
                        </div>
                    </div>
                </div>
            </div>
            <div class="bottom">
                <div class="item">
                    <van-icon name="manager-o" />
                    联系商家
                </div>
                <div class="item">
                    <van-icon name="peer-pay" />
                    进店逛逛
                </div>
            </div>
        </div>
        <div class="hr" style="height:.5rem"></div>
        <van-goods-action>
            <van-goods-action-mini-btn
                icon="chat-o"
                text="客服"
            />
            <van-goods-action-mini-btn
                info="5"
                icon="cart-o"
                text="购物车"
            />
            <van-goods-action-mini-btn
                icon="shop-o"
                text="店铺"
            />
            <van-goods-action-big-btn text="加入购物车" />
            <van-goods-action-big-btn
                primary
                text="立即购买"
            />
        </van-goods-action>
    </div>
</template>

<script>
import VHeader  from './components/Header'
    export default {
       components:{
           VHeader
       },
       data() {
           return {
                swiperOption: {
                // some swiper options/callbacks
                // 所有的参数同 swiper 官方 api 参数
                // ...
                }
            }
       },
       methods:{
           callback(){}
       }
    }
</script>

<style lang="stylus" scoped>
.goods
    width 100%
    .hr
        height: 0.04rem;
        width: 100%;
        background: rgba(239,239,239,1.00);
    .swiper
        width 100%
        .swiper-wrapper
            img 
                width 3.75rem
                // height 1.80rem
        .swiper-pagination-bullet-active
            background-color rgb(234,41,106)
    .good_info
        padding .15rem
        .price
            width 100%
            height .24rem
            color rgb(236,72,125)
        .price2
            color rgb(156,156,156)
            font-size .14rem
        .col
            .title
                height .3rem
                font-size .16rem
                line-height .3rem
            .text_pice
                font-size .12rem
                color rgb(236,72,125)   
    .fujia
        padding .15rem
        .row
            height .26rem
            display flex
            color #6c757d
            font-size .14rem
            .xs2
                flex 2
            .xs10
                flex 10 
    .shop
        padding 0 .15rem
        .top
            display flex
            padding .15rem
            .img
                flex 2
                img 
                    width 100%
            .info
                flex 10
                padding-left .1rem
                display flex
                flex-direction column
                justify-content space-around
                .title
                    .shop_logo
                        color #ffffff
                        background-color rgb(236,72,125)
                        border-radius 50%
                        font-size .14rem
                        width .19rem
                        height .19rem
                        display inline-block
                .shop_list
                    display flex
                    
                    .item
                        flex 1
                        font-size .12rem
                        .text_pice
                            color rgb(236,72,125)
        .bottom
            width 100%
            display flex
            justify-content space-around
            align-items center
            // .item

                
    
</style>


